import React from 'react';
import type { HudState } from '../types';

interface HUDProps {
  currentLap: number;
  availableLaps: number;
  points: number;
  hud: HudState;
  gameState: 'playing' | 'paused' | 'gameover' | 'loading' | 'ready';
}

export const HUD: React.FC<HUDProps> = ({ currentLap, availableLaps, points, hud, gameState }) => {
  /**
   * Calculate speedometer rotation
   */
  const getSpeedometerRotation = () => {
    const speed = parseInt(hud.speed || '0') || 0;
    // Rotate needle between -90 and 90 degrees based on speed
    return (speed / 100) * 180 - 90;
  };

  if (gameState !== 'playing' && gameState !== 'paused' && gameState !== 'loading') {
    return null;
  }

  return (
    <>
      {/* Top HUD */}
      <div className="hud-top">
        {/* Lap Counter */}
        <span className="hud-lap-counter">
          <img className="hud-lap-icon" src="/games/racing/images/flag-laps.webp" alt="laps" />
          <span className="hud-lap-text">
            {currentLap}/{availableLaps}
          </span>
        </span>

        {/* Lap Time */}
        <span className="hud-time">
          <img className="hud-lap-icon" src="/games/racing/images/cronometer.webp" alt="time" />
          <span className="hud-lap-text">{hud.current_lap_time}</span>
        </span>

        {/* Points */}
        <span className="hud-score">
          <img className="hud-lap-icon" src="/games/racing/images/coin.webp" alt="points" />
          <span className="hud-score-value">{points}</span>
        </span>
      </div>

      {/* Bottom HUD - Speedometer */}
      <div className="hud-bottom">
        <div className="hud-speed">
          <svg className="hud-speedometer" viewBox="0 0 200 200">
            {/* Background circle */}
            <circle cx="100" cy="100" r="90" fill="none" stroke="#444" strokeWidth="2" />
            {/* Speedometer arc */}
            <path d="M 30 170 A 85 85 0 0 1 170 170" fill="none" stroke="#888" strokeWidth="8" />
            {/* Speed markers */}
            {[0, 25, 50, 75, 100].map((speed, i) => {
              const angle = ((i * 180) / 4 - 90) * (Math.PI / 180);
              const x1 = 100 + 70 * Math.cos(angle);
              const y1 = 100 + 70 * Math.sin(angle);
              const x2 = 100 + 80 * Math.cos(angle);
              const y2 = 100 + 80 * Math.sin(angle);
              return (
                <line key={speed} x1={x1} y1={y1} x2={x2} y2={y2} stroke="#ccc" strokeWidth="2" />
              );
            })}
            {/* Speed needle */}
            <line
              x1="100"
              y1="100"
              x2={100 + 65 * Math.cos((getSpeedometerRotation() - 90) * (Math.PI / 180))}
              y2={100 + 65 * Math.sin((getSpeedometerRotation() - 90) * (Math.PI / 180))}
              stroke="#ff4444"
              strokeWidth="4"
              strokeLinecap="round"
            />
            {/* Center dot */}
            <circle cx="100" cy="100" r="5" fill="#ff4444" />
          </svg>
          {/* Speed value */}
          <div className="hud-speed-text">{hud.speed} mph</div>
        </div>
      </div>
    </>
  );
};
