'use client';
import { useCallback, useEffect, useState } from 'react';
import { useCarGame } from './hooks/useCarGame';
import { HUD } from './components/HUD';
import { BillboardStats } from './components/BillboardStats';
import { GameCanvas } from '../ui/GameCanvas';
import { GameOverlay } from '../ui/GameOverlay';
import { GameControls } from '../ui/GameControls';
import { isMobile } from '../../../../utils/functions';
import { Language } from '../../../../types/i18n';
import { getTranslations } from '../../../../i18n';

interface RacingGameProps {
  laps: number;
  setPointsEarned: (points: number) => void;
  setGameOver: (isOver: boolean) => void;
  /**
   * Track layout to use:
   * 0 (default) - Original balanced track with variety
   * 1 - Speed Circuit: Fast track with long straights
   * 2 - Mountain Pass: Challenging hills and curves
   * 3 - Technical Circuit: Lots of curves and turns
   */
  track?: number;
  skin?: string;
  gameTitle?: string;
  brandColor?: string;
  brandLogo?: string;
  showBillboardStats?: boolean;
  lang?: Language;
}

const RacingGame = ({
  laps,
  setPointsEarned,
  setGameOver,
  track = 0,
  skin = 'default',
  gameTitle = 'JALO Racing',
  brandColor = '#00d26a',
  brandLogo,
  showBillboardStats = false,
  lang = 'en',
}: RacingGameProps) => {
  const t = getTranslations(lang);
  const mobile = isMobile();
  const availableLaps = laps || 3;
  const [gameStarted, setGameStarted] = useState(false);

  // Use the new custom hook for game logic
  const {
    canvasRef,
    gameState,
    currentLap,
    points,
    hud,
    startGame,
    togglePause,
    setupTouchControls,
    handleTouchControl,
    getBillboardStats,
  } = useCarGame(availableLaps, track, skin, setPointsEarned, setGameOver);

  // Start game when instructions are dismissed
  const handleGameStart = async () => {
    setGameStarted(true);
    await startGame();
  };

  // Initialize canvas on mount
  useEffect(() => {
    if (canvasRef.current) {
      // Set initial canvas dimensions
      canvasRef.current.width = 720;
      canvasRef.current.height = 960;
    }
  }, [canvasRef]);

  // Setup touch controls when game starts on mobile
  useEffect(() => {
    if (mobile && gameState === 'playing') {
      setupTouchControls(true);
    }
  }, [mobile, gameState, setupTouchControls]);

  // Handle control input from GameControls
  const handleControl = useCallback(
    (direction: 'up' | 'down' | 'left' | 'right' | 'action' | 'action2') => {
      if (gameState !== 'playing') return;

      // Directly update the input state through the hook
      handleTouchControl(direction, true);
    },
    [gameState, handleTouchControl]
  );

  const handleControlEnd = useCallback(
    (direction: 'up' | 'down' | 'left' | 'right' | 'action' | 'action2') => {
      // Directly update the input state through the hook
      handleTouchControl(direction, false);
    },
    [handleTouchControl]
  );

  // Handle keyboard shortcuts
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      // Prevent arrow key scrolling
      const keys: { [key: number]: number } = { 37: 1, 38: 1, 39: 1, 40: 1 };
      if (keys[e.keyCode]) {
        e.preventDefault();
        return false;
      }

      // Handle pause (ESC or Space)
      if (gameStarted && (e.key === 'Escape' || e.key === ' ')) {
        e.preventDefault();
        togglePause();
      }
    };

    window.addEventListener('keydown', handleKeyDown, false);
    return () => {
      window.removeEventListener('keydown', handleKeyDown, false);
    };
  }, [gameStarted, togglePause]);

  return (
    <div style={{ width: '100%', position: 'relative' }}>
      <div
        style={{
          width: '100%',
          position: 'relative',
          display: 'flex',
          flexDirection: 'column',
          gap: '0.25rem',
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        {/* Instructions Overlay */}
        {!gameStarted && (
          <GameOverlay
            type="custom"
            title={gameTitle}
            brandColor={brandColor}
            onAction={handleGameStart}
            actionText={t.games.racing.startRacing}
            lang={lang}
            customContent={
              <div style={{ marginBottom: '1rem' }}>
                {brandLogo && (
                  <>
                    <span style={{ fontSize: '0.75rem', color: 'white' }}>por:</span>
                    <img
                      src={brandLogo}
                      alt="Brand Logo"
                      style={{
                        margin: '0 auto',
                        width: '12rem',
                        height: 'auto',
                        marginBottom: '1rem',
                      }}
                    />
                  </>
                )}
                <p style={{ fontSize: '0.875rem', color: '#d1d5db' }}>
                  {mobile
                    ? t.games.racing.useTouchControls
                    : t.games.racing.useKeyboard}
                </p>

                <div style={{ marginTop: '0.5rem' }}>
                  {mobile ? (
                    <>
                      <div
                        style={{
                          display: 'flex',
                          alignItems: 'center',
                          gap: '0.75rem',
                          marginBottom: '0.5rem',
                        }}
                      >
                        <kbd
                          style={{
                            padding: '0.5rem 0.75rem',
                            backgroundColor: '#374151',
                            borderRadius: '0.25rem',
                            color: 'white',
                            fontWeight: 'bold',
                            fontSize: '0.875rem',
                          }}
                        >
                          
                        </kbd>
                        <span style={{ fontSize: '0.875rem', color: '#d1d5db' }}>
                          {t.games.racing.turnLeft}
                        </span>
                      </div>
                      <div
                        style={{
                          display: 'flex',
                          alignItems: 'center',
                          gap: '0.75rem',
                          marginBottom: '0.5rem',
                        }}
                      >
                        <kbd
                          style={{
                            padding: '0.5rem 0.75rem',
                            backgroundColor: '#374151',
                            borderRadius: '0.25rem',
                            color: 'white',
                            fontWeight: 'bold',
                            fontSize: '0.875rem',
                          }}
                        >
                          
                        </kbd>
                        <span style={{ fontSize: '0.875rem', color: '#d1d5db' }}>
                          {t.games.racing.turnRight}
                        </span>
                      </div>
                      <div
                        style={{
                          display: 'flex',
                          alignItems: 'center',
                          gap: '0.75rem',
                          marginBottom: '0.5rem',
                        }}
                      >
                        <kbd
                          style={{
                            padding: '0.5rem 0.75rem',
                            backgroundColor: '#059669',
                            borderRadius: '0.25rem',
                            color: 'white',
                            fontWeight: 'bold',
                            fontSize: '0.875rem',
                          }}
                        >
                          
                        </kbd>
                        <span style={{ fontSize: '0.875rem', color: '#d1d5db' }}>{t.games.racing.accelerate}</span>
                      </div>
                      <div
                        style={{
                          display: 'flex',
                          alignItems: 'center',
                          gap: '0.75rem',
                          marginBottom: '0.5rem',
                        }}
                      >
                        <kbd
                          style={{
                            padding: '0.5rem 0.75rem',
                            backgroundColor: '#dc2626',
                            borderRadius: '0.25rem',
                            color: 'white',
                            fontWeight: 'bold',
                            fontSize: '0.875rem',
                          }}
                        >
                          
                        </kbd>
                        <span style={{ fontSize: '0.875rem', color: '#d1d5db' }}>{t.games.racing.brake}</span>
                      </div>
                    </>
                  ) : (
                    <>
                      <div
                        style={{
                          display: 'flex',
                          alignItems: 'center',
                          gap: '0.75rem',
                          marginBottom: '0.5rem',
                        }}
                      >
                        <kbd
                          style={{
                            padding: '0.25rem 0.5rem',
                            backgroundColor: '#374151',
                            borderRadius: '0.25rem',
                            fontSize: '0.75rem',
                            color: 'white',
                          }}
                        >
                          
                        </kbd>
                        <span style={{ fontSize: '0.875rem', color: '#d1d5db' }}>{t.games.racing.or}</span>
                        <kbd
                          style={{
                            padding: '0.25rem 0.5rem',
                            backgroundColor: '#374151',
                            borderRadius: '0.25rem',
                            fontSize: '0.75rem',
                            color: 'white',
                          }}
                        >
                          A
                        </kbd>
                        <span style={{ fontSize: '0.875rem', color: '#d1d5db' }}>
                          - {t.games.racing.turnLeft}
                        </span>
                      </div>
                      <div
                        style={{
                          display: 'flex',
                          alignItems: 'center',
                          gap: '0.75rem',
                          marginBottom: '0.5rem',
                        }}
                      >
                        <kbd
                          style={{
                            padding: '0.25rem 0.5rem',
                            backgroundColor: '#374151',
                            borderRadius: '0.25rem',
                            fontSize: '0.75rem',
                            color: 'white',
                          }}
                        >
                          
                        </kbd>
                        <span style={{ fontSize: '0.875rem', color: '#d1d5db' }}>{t.games.racing.or}</span>
                        <kbd
                          style={{
                            padding: '0.25rem 0.5rem',
                            backgroundColor: '#374151',
                            borderRadius: '0.25rem',
                            fontSize: '0.75rem',
                            color: 'white',
                          }}
                        >
                          D
                        </kbd>
                        <span style={{ fontSize: '0.875rem', color: '#d1d5db' }}>
                          - {t.games.racing.turnRight}
                        </span>
                      </div>
                      <div
                        style={{
                          display: 'flex',
                          alignItems: 'center',
                          gap: '0.75rem',
                          marginBottom: '0.5rem',
                        }}
                      >
                        <kbd
                          style={{
                            padding: '0.25rem 0.5rem',
                            backgroundColor: '#374151',
                            borderRadius: '0.25rem',
                            fontSize: '0.75rem',
                            color: 'white',
                          }}
                        >
                          
                        </kbd>
                        <span style={{ fontSize: '0.875rem', color: '#d1d5db' }}>{t.games.racing.or}</span>
                        <kbd
                          style={{
                            padding: '0.25rem 0.5rem',
                            backgroundColor: '#374151',
                            borderRadius: '0.25rem',
                            fontSize: '0.75rem',
                            color: 'white',
                          }}
                        >
                          W
                        </kbd>
                        <span style={{ fontSize: '0.875rem', color: '#d1d5db' }}>- {t.games.racing.accelerate}</span>
                      </div>
                      <div
                        style={{
                          display: 'flex',
                          alignItems: 'center',
                          gap: '0.75rem',
                          marginBottom: '0.5rem',
                        }}
                      >
                        <kbd
                          style={{
                            padding: '0.25rem 0.5rem',
                            backgroundColor: '#374151',
                            borderRadius: '0.25rem',
                            fontSize: '0.75rem',
                            color: 'white',
                          }}
                        >
                          
                        </kbd>
                        <span style={{ fontSize: '0.875rem', color: '#d1d5db' }}>{t.games.racing.or}</span>
                        <kbd
                          style={{
                            padding: '0.25rem 0.5rem',
                            backgroundColor: '#374151',
                            borderRadius: '0.25rem',
                            fontSize: '0.75rem',
                            color: 'white',
                          }}
                        >
                          S
                        </kbd>
                        <span style={{ fontSize: '0.875rem', color: '#d1d5db' }}>- {t.games.racing.brake}</span>
                      </div>
                    </>
                  )}
                </div>

                <p style={{ fontSize: '0.75rem', color: '#9ca3af', marginTop: '1rem' }}>
                  {t.games.racing.collectCoins}
                </p>
              </div>
            }
          />
        )}

        {/* Pause Overlay */}
        {gameState === 'paused' && (
          <GameOverlay
            type="pause"
            title={` ${t.games.pause}`}
            message={t.games.racing.pressEscContinue}
            onAction={togglePause}
            actionText={t.games.continue}
            brandColor={brandColor}
            lang={lang}
          />
        )}

        {/* Game Area */}
        <div
          id="racer"
          style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%' }}
        >
          {gameState === 'gameover' && (
            <GameOverlay type="gameOver" score={points} brandColor={brandColor} lang={lang} />
          )}

          {/* Game Canvas Container - always present but hidden during ready state */}
          <div
            style={{
              position: 'relative',
              padding: 0,
              margin: 0,
              width: '100%',
              maxWidth: '360px',
              height: '480px',
              display: gameState === 'gameover' ? 'none' : 'block',
              visibility: gameState === 'ready' ? 'hidden' : 'visible',
            }}
          >
            {/* HUD Component */}
            <HUD
              currentLap={currentLap}
              availableLaps={availableLaps}
              points={points}
              hud={hud}
              gameState={gameState}
            />

            {/* Game Canvas */}
            <GameCanvas
              ref={canvasRef}
              width={720}
              height={960}
              style={{ backgroundColor: '#35cafa', width: '100%', height: '100%' }}
            />
          </div>
        </div>

        {/* Billboard Statistics (Debug Mode) */}
        <BillboardStats
          getBillboardStats={getBillboardStats}
          isVisible={showBillboardStats && gameState === 'playing' && !mobile}
        />

        {/* Game Controls */}
        {mobile && gameState === 'playing' && (
          <GameControls
            onControl={handleControl}
            onControlEnd={handleControlEnd}
            layout="racing"
            brandColor={brandColor}
            actionIcon=""
            action2Icon=""
          />
        )}
      </div>
    </div>
  );
};

export default RacingGame;
