import React from 'react';

interface GameControlsProps {
  onControl: (direction: 'up' | 'down' | 'left' | 'right' | 'action' | 'action2') => void;
  onControlEnd?: (direction: 'up' | 'down' | 'left' | 'right' | 'action' | 'action2') => void;
  brandColor?: string;
  showActionButton?: boolean;
  actionLabel?: string;
  layout?: 'dpad' | 'arrows' | 'horizontal' | 'racing';
  actionIcon?: React.ReactNode;
  action2Label?: string;
  action2Icon?: React.ReactNode;
}

export const GameControls: React.FC<GameControlsProps> = ({
  onControl,
  onControlEnd,
  showActionButton = false,
  layout = 'dpad',
  actionLabel = 'A',
  actionIcon,
  action2Label = 'B',
  action2Icon,
}) => {
  // Game Boy inspired button styling with text selection prevention
  const buttonClass = `
    font-bold text-2xl rounded-lg
    transition-all duration-150 transform
    hover:scale-105 active:scale-95
    bg-gray-600 text-gray-100
    border-2 border-gray-800
    shadow-[inset_0_-4px_0_rgba(0,0,0,0.4),0_2px_0_rgba(255,255,255,0.2)]
    active:shadow-[inset_0_-2px_0_rgba(0,0,0,0.4),0_1px_0_rgba(255,255,255,0.2)]
    select-none
  `;

  // Common styles for preventing text selection
  const preventSelectStyles: React.CSSProperties = {
    userSelect: 'none',
    WebkitUserSelect: 'none',
    MozUserSelect: 'none',
    msUserSelect: 'none',
    touchAction: 'none',
    WebkitTouchCallout: 'none',
  };

  const handleContextMenu = (e: React.MouseEvent) => {
    e.preventDefault();
    return false;
  };

  // Horizontal layout for games like Breakout
  if (layout === 'horizontal') {
    return (
      <div className="flex justify-center gap-6 p-6">
        <button
          onTouchStart={(e) => {
            e.preventDefault();
            onControl('left');
          }}
          onClick={() => onControl('left')}
          onContextMenu={handleContextMenu}
          className={`${buttonClass} px-8 py-4 text-3xl`}
          style={preventSelectStyles}
        >
          
        </button>
        {showActionButton && (
          <button
            onTouchStart={(e) => {
              e.preventDefault();
              onControl('action');
            }}
            onClick={() => onControl('action')}
            onContextMenu={handleContextMenu}
            className={`${buttonClass} px-8 py-4 rounded-full`}
            style={preventSelectStyles}
          >
            {actionIcon || actionLabel}
          </button>
        )}
        <button
          onTouchStart={(e) => {
            e.preventDefault();
            onControl('right');
          }}
          onClick={() => onControl('right')}
          onContextMenu={handleContextMenu}
          className={`${buttonClass} px-8 py-4 text-3xl`}
          style={preventSelectStyles}
        >
          
        </button>
      </div>
    );
  }

  // Racing layout with steering controls on left and action buttons on right
  if (layout === 'racing') {
    return (
      <div className="flex justify-between items-center p-4 w-full max-w-md mx-auto">
        {/* Steering controls on the left */}
        <div className="flex gap-2">
          <button
            onTouchStart={(e) => {
              e.preventDefault();
              onControl('left');
            }}
            onTouchEnd={(e) => {
              e.preventDefault();
              onControlEnd?.('left');
            }}
            onMouseDown={() => onControl('left')}
            onMouseUp={() => onControlEnd?.('left')}
            onContextMenu={handleContextMenu}
            className={`${buttonClass} w-16 h-16 sm:w-20 sm:h-20 text-2xl`}
            style={preventSelectStyles}
          >
            
          </button>
          <button
            onTouchStart={(e) => {
              e.preventDefault();
              onControl('right');
            }}
            onTouchEnd={(e) => {
              e.preventDefault();
              onControlEnd?.('right');
            }}
            onMouseDown={() => onControl('right')}
            onMouseUp={() => onControlEnd?.('right')}
            onContextMenu={handleContextMenu}
            className={`${buttonClass} w-16 h-16 sm:w-20 sm:h-20 text-2xl`}
            style={preventSelectStyles}
          >
            
          </button>
        </div>

        {/* Action buttons on the right */}
        <div className="flex flex-col gap-2">
          <button
            onTouchStart={(e) => {
              e.preventDefault();
              onControl('up');
            }}
            onTouchEnd={(e) => {
              e.preventDefault();
              onControlEnd?.('up');
            }}
            onMouseDown={() => onControl('up')}
            onMouseUp={() => onControlEnd?.('up')}
            onContextMenu={handleContextMenu}
            className={`${buttonClass} w-16 h-12 sm:w-20 sm:h-14 text-lg bg-green-600 hover:bg-green-700`}
            style={preventSelectStyles}
          >
            {actionIcon || actionLabel}
          </button>
          <button
            onTouchStart={(e) => {
              e.preventDefault();
              onControl('down');
            }}
            onTouchEnd={(e) => {
              e.preventDefault();
              onControlEnd?.('down');
            }}
            onMouseDown={() => onControl('down')}
            onMouseUp={() => onControlEnd?.('down')}
            onContextMenu={handleContextMenu}
            className={`${buttonClass} w-16 h-12 sm:w-20 sm:h-14 text-lg bg-red-600 hover:bg-red-700`}
            style={preventSelectStyles}
          >
            {action2Icon || action2Label}
          </button>
        </div>
      </div>
    );
  }

  if (layout === 'arrows') {
    return (
      <div className="flex items-center justify-center p-8">
        <div className="relative w-36 sm:w-48 h-36 sm:h-48">
          {/* Background Circle */}
          <div className="absolute inset-0 rounded-full bg-gray-700 opacity-30" />

          {/* Center Circle */}
          <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-12 h-12 sm:w-16 sm:h-16 bg-gray-800 rounded-full border-2 border-gray-900 shadow-inner" />
          {/* Arrow Buttons */}
          <button
            onTouchStart={(e) => {
              e.preventDefault();
              onControl('up');
            }}
            onTouchEnd={(e) => {
              e.preventDefault();
              onControlEnd?.('up');
            }}
            onMouseDown={() => onControl('up')}
            onMouseUp={() => onControlEnd?.('up')}
            onContextMenu={handleContextMenu}
            className={`${buttonClass} absolute top-0 left-1/2 transform -translate-x-1/2 w-12 h-12 sm:w-16 sm:h-16 rounded-t-3xl rounded-b-lg`}
            style={preventSelectStyles}
          >
            
          </button>

          <button
            onTouchStart={(e) => {
              e.preventDefault();
              onControl('left');
            }}
            onTouchEnd={(e) => {
              e.preventDefault();
              onControlEnd?.('left');
            }}
            onMouseDown={() => onControl('left')}
            onMouseUp={() => onControlEnd?.('left')}
            onContextMenu={handleContextMenu}
            className={`${buttonClass} absolute left-0 top-1/2 transform -translate-y-1/2 w-12 h-12 sm:w-16 sm:h-16 rounded-l-3xl rounded-r-lg`}
            style={preventSelectStyles}
          >
            
          </button>

          {showActionButton && (
            <button
              onTouchStart={(e) => {
                e.preventDefault();
                onControl('action');
              }}
              onClick={() => onControl('action')}
              onContextMenu={handleContextMenu}
              className={`${buttonClass} col-start-2 row-start-2 w-12 h-12 sm:w-16 sm:h-16 rounded-full`}
              style={preventSelectStyles}
            >
              {actionIcon || actionLabel}
            </button>
          )}

          <button
            onTouchStart={(e) => {
              e.preventDefault();
              onControl('right');
            }}
            onTouchEnd={(e) => {
              e.preventDefault();
              onControlEnd?.('right');
            }}
            onMouseDown={() => onControl('right')}
            onMouseUp={() => onControlEnd?.('right')}
            onContextMenu={handleContextMenu}
            className={`${buttonClass} absolute right-0 top-1/2 transform -translate-y-1/2 w-12 h-12 sm:w-16 sm:h-16 rounded-r-3xl rounded-l-lg`}
            style={preventSelectStyles}
          >
            
          </button>

          <button
            onTouchStart={(e) => {
              e.preventDefault();
              onControl('down');
            }}
            onTouchEnd={(e) => {
              e.preventDefault();
              onControlEnd?.('down');
            }}
            onMouseDown={() => onControl('down')}
            onMouseUp={() => onControlEnd?.('down')}
            onContextMenu={handleContextMenu}
            className={`${buttonClass} absolute bottom-0 left-1/2 transform -translate-x-1/2 w-12 h-12 sm:w-16 sm:h-16 rounded-b-3xl rounded-t-lg`}
            style={preventSelectStyles}
          >
            
          </button>
        </div>
      </div>
    );
  }

  // D-Pad layout
  return (
    <div className="flex items-center justify-center p-8">
      <div className="relative w-36 sm:w-48 h-36 sm:h-48">
        {/* D-Pad Background */}
        <div className="absolute inset-0 rounded-full bg-gray-700 opacity-30" />

        {/* Center Circle */}
        <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-12 h-12 sm:w-16 sm:h-16 bg-gray-800 rounded-full border-2 border-gray-900 shadow-inner" />

        {/* D-Pad Buttons */}
        <button
          onTouchStart={(e) => {
            e.preventDefault();
            onControl('up');
          }}
          onTouchEnd={(e) => {
            e.preventDefault();
            onControlEnd?.('up');
          }}
          onMouseDown={() => onControl('up')}
          onMouseUp={() => onControlEnd?.('up')}
          onContextMenu={handleContextMenu}
          className={`${buttonClass} absolute top-0 left-1/2 transform -translate-x-1/2 w-12 h-12 sm:w-16 sm:h-16 rounded-t-3xl rounded-b-lg`}
          style={preventSelectStyles}
        >
          
        </button>

        <button
          onTouchStart={(e) => {
            e.preventDefault();
            onControl('right');
          }}
          onClick={() => onControl('right')}
          onContextMenu={handleContextMenu}
          className={`${buttonClass} absolute right-0 top-1/2 transform -translate-y-1/2 w-12 h-12 sm:w-16 sm:h-16 rounded-r-3xl rounded-l-lg`}
          style={preventSelectStyles}
        >
          
        </button>

        <button
          onTouchStart={(e) => {
            e.preventDefault();
            onControl('down');
          }}
          onTouchEnd={(e) => {
            e.preventDefault();
            onControlEnd?.('down');
          }}
          onMouseDown={() => onControl('down')}
          onMouseUp={() => onControlEnd?.('down')}
          onContextMenu={handleContextMenu}
          className={`${buttonClass} absolute bottom-0 left-1/2 transform -translate-x-1/2 w-12 h-12 sm:w-16 sm:h-16 rounded-b-3xl rounded-t-lg`}
          style={preventSelectStyles}
        >
          
        </button>

        <button
          onTouchStart={(e) => {
            e.preventDefault();
            onControl('left');
          }}
          onClick={() => onControl('left')}
          onContextMenu={handleContextMenu}
          className={`${buttonClass} absolute left-0 top-1/2 transform -translate-y-1/2 w-12 h-12 sm:w-16 sm:h-16 rounded-l-3xl rounded-r-lg`}
          style={preventSelectStyles}
        >
          
        </button>

        {/* Action Button */}
        {showActionButton && (
          <button
            onTouchStart={(e) => {
              e.preventDefault();
              onControl('action');
            }}
            onClick={() => onControl('action')}
            onContextMenu={handleContextMenu}
            className={`${buttonClass} absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-12 h-12 rounded-full z-10`}
            style={preventSelectStyles}
          >
            {actionIcon || actionLabel}
          </button>
        )}
      </div>
    </div>
  );
};
