import React from 'react';

interface ThemeDropdownProps {
  currentTheme: string;
  onSelectTheme: (theme: string) => void;
  themes: {
    classic: string;
    vaporwave: string;
    terminal: string;
  };
  variant?: 'menubar' | 'statusbar';
}

const ThemeDropdown: React.FC<ThemeDropdownProps> = ({
  currentTheme,
  onSelectTheme,
  themes,
  variant = 'menubar',
}) => {
  const className = variant === 'statusbar' ? 'status-theme-dropdown' : 'theme-dropdown';

  return (
    <div className={className}>
      <button
        className={currentTheme === 'classic' ? 'active' : ''}
        onClick={() => onSelectTheme('classic')}
      >
        {themes.classic}
      </button>
      <button
        className={currentTheme === 'vaporwave' ? 'active' : ''}
        onClick={() => onSelectTheme('vaporwave')}
      >
        {themes.vaporwave}
      </button>
      <button
        className={currentTheme === 'terminal' ? 'active' : ''}
        onClick={() => onSelectTheme('terminal')}
      >
        {themes.terminal}
      </button>
    </div>
  );
};

export default ThemeDropdown;
