//Funcion para determinar si el usuario esta en un dispositivo movil
export const isMobile = () => {
  if (typeof window !== 'undefined') {
    const isMobileUserAgent = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
      navigator.userAgent
    );
    const isMobileWidth = window.matchMedia('only screen and (max-width: 768px)').matches;
    const moreThanOneTouchPoint = navigator.maxTouchPoints > 1; //cuantos puntos de contacto tiene el dispositivo
    return isMobileWidth && isMobileUserAgent && moreThanOneTouchPoint;
  }
  return false;
};
