import React from 'react';
import './PageNavigation.css';
import { Language } from '../../../types/i18n';
import { getTranslations } from '../../../i18n';

interface PageNavigationProps {
  currentSection: string;
  onNavigate: (action: string) => void;
  lang?: Language;
}

const PageNavigation: React.FC<PageNavigationProps> = ({ currentSection, onNavigate, lang = 'en' }) => {
  const t = getTranslations(lang);
  const navItems = [
    { id: '', label: t.navigation.home, icon: '' },
    { id: 'goTo-about', label: t.navigation.about, icon: '' },
    { id: 'goTo-work', label: t.navigation.work, icon: '' },
    { id: 'goTo-portfolio', label: t.navigation.portfolio, icon: '' },
    { id: 'goTo-contact', label: t.navigation.contact, icon: '' },
    { id: 'goTo-games', label: t.navigation.games, icon: '' },
  ];

  const getActiveClass = (itemId: string) => {
    return currentSection === itemId ? 'active' : '';
  };

  return (
    <div className="page-navigation">
      <div className="nav-container">
        {navItems.map((item) => (
          <button
            key={item.id}
            className={`nav-item ${getActiveClass(item.id)}`}
            onClick={() => onNavigate(item.id)}
            aria-current={currentSection === item.id ? 'page' : undefined}
          >
            <span className="nav-icon">{item.icon}</span>
            <span className="nav-label">{item.label}</span>
          </button>
        ))}
      </div>
    </div>
  );
};

export default PageNavigation;
